import React, { useEffect, useState } from 'react';
import '../styles/Banner.css'
import requests from '../api/requests';
import axios from '../api/axios';
import styled from 'styled-components';

const Banner = () => {
    //useState  : movie   
    const [movie, setMovie] = useState([]);

    //API    .    false .
    const [loading, setLoading] = useState(true);

    //    
    const [isClicked, setIsClicked] = useState(false);

    //      '[]'  fetchData()   
    useEffect(() => {
        fetchData();
    }, []);

    //fetchData()   : movieId  movie detail  
    const fetchData = async () => {

        //   
        
        try {
            setLoading(true);

            /* await axios.get  http get       */
            const request = await axios.get(requests.fetchNowPlaying);
            //console.log("  ", request.data);

            //  
            const validMovies = request.data.results.filter(
                movie => movie.backdrop_path
            );

            //  
            const randomMovieId =
                validMovies[
                    //floor() :  
                    Math.floor(Math.random() * validMovies.length)
                ]?.id;

            if (randomMovieId) {
                const { data: movieDetail } = await axios.get(
                    `movie/${randomMovieId}`,
                    {
                        params: { append_to_response: 'videos' },
                    });
                setMovie(movieDetail);
            }
        } catch (error) {
            console.error("  .", error);
        }
        finally {
            setLoading(false); //  
        }
    };

    //  
    if (!isClicked) {
        return (
            <section className="main-banner"
                style={{
                    backgroundImage: movie?.backdrop_path
                        ? `url("https://image.tmdb.org/t/p/original/${movie.backdrop_path}")`
                        : `url("/default-banner-image.jpg")`, backgroundSize: 'cover',
                    backgroundPosition: 'Top center',
                }}
            >
                {loading ? (
                    <div className="loader"> ...</div>
                ) : (
                    <div className="banner-content">
                        <h1 className='banner-titile'>{movie?.title || movie?.name || movie?.original_name}</h1>
                        <div className="buttons">
                            <button className="banner-play-btn"
                                onClick={() => setIsClicked(true)}></button>
                            <button className="banner-detail-btn"> </button>
                        </div>
                        <h2 className="banner-description">{movie.overview}</h2>
                    </div>
                )}
            </section>
        );
    } else {
        // Iframe  
        return (
            <Container>
                <HomeContainer>
                    <Iframe
                        src={`https://www.youtube.com/embed/${movie.videos.results[0]?.key}
                        ?controls=0&autoplay=1&loop=1&mute=1&playlist=${movie.videos.results[0]?.key}`}
                        width="640"
                        height="360"
                        framborder="0"
                        allow="autoplay; fullscreen">
                    </Iframe>
                </HomeContainer>
            </Container>
        )
    }
};

export default Banner;

// styled-components Banner  
const Container = styled.div`
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
    width: 100%;
    flex-direction: column;`;

const HomeContainer = styled.div`
    width: 100%;
    height: 100%;`;

const Iframe = styled.iframe`
    width: 100%;
    height: 100%;
    z-index: 1;
    border: none;
    opacity: 0.65;

    &::after {
        content: "";
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;    
    }
`;