import React, { useState, useEffect } from 'react';
import '../styles/Nav.css'
import { useNavigate } from 'react-router-dom';

export default function Nav() {
    //   
    const [show, setShow] = useState(false);
    
    //  input &
    const [search, setSearch] = useState(false);
    
    //   
    const [searchInput, setSearchInput] = useState("");

    const navigate = useNavigate(); //    hook

    useEffect(() => {
        const handleScroll = () => {
            if (window.scrollY > 50) {
                setShow(true);
            } else {
                setShow(false);
            }
        };
        window.addEventListener('scroll', handleScroll);
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []);

    const toggleSearch = () => {
        setSearch(!search);
    }

    const handleSearchSubmit = (e) => {
        e.preventDefault(); //    
        console.log(":", searchInput); //  
        if (searchInput.trim()) {
            navigate(`/search?q=${searchInput}`); //   
        }
    };

    const handleLogoClick = () => {
        navigate('/'); //    
    };

    return (
        <nav className={`nav ${show && 'nav-black'}`}>
            <img 
                className='nav-logo'
                src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/400px-Netflix_2015_logo.svg.png"
                alt="netflix-logo"
                onClick={handleLogoClick}
            />

            <div className="nav-menu">
                <a href='http://localhost:3000/'></a>
                <a href='http://localhost:3000/'></a>
                <a href='http://localhost:3000/'></a>
                <a href='http://localhost:3000/'>NEW!   </a>
                <a href='http://localhost:3000/'>  </a>
                <a href='http://localhost:3000/'> </a>
            </div>

            {search && ( // input  
                    <form onSubmit={handleSearchSubmit}>
                    <input 
                        type="text" 
                        className="search-input" 
                        placeholder=" ..."
                        value={searchInput}
                        onChange={(e) => setSearchInput(e.target.value)} //   
                    />
                    </form>
                )}
            <img
                className='nav-search'
                src={process.env.PUBLIC_URL + '/search.png'}
                alt="search-icon"
                onClick={toggleSearch}
            />
            <img
                className='nav-alarm'
                src={process.env.PUBLIC_URL + '/alarm.png'}
                alt="alarm-icon"
            />
            <img
                className='nav-avatar'
                src={process.env.PUBLIC_URL + '/Netflix-avatar.png'}
                alt="netflix-avatar"
            />
        </nav>
)
}
