import React, { useEffect, useState } from 'react'
import { useLocation, useNavigate } from'react-router-dom';
import axios from '../../api/axios';
import './SearchPage.css';
import { useDebounce } from '../../hooks/useDebounce';

export default function SearchPage() {
  const navigate = useNavigate();
  const [searchResults, setSearchResults] = useState([]);
  const useQuery = () => {
    return new URLSearchParams(useLocation().search);
  }

  // search search term 
  let query = useQuery();
  const searchTerm = query.get('q');
  const debouncedSearchTerm = useDebounce(query.get("q"), 500);

  useEffect(() => {
    if (debouncedSearchTerm) {
      fetchSearchMovie(debouncedSearchTerm);
    }
  }, [debouncedSearchTerm]);

  const fetchSearchMovie = async (searchTerm) => {
    try {
      const request = await axios.get(
        `/search/multi?include_adult=false&query=${searchTerm}`
      );
      console.log(request.data.results); // API  
      setSearchResults(request.data.results);

    }
    catch (error) {
      console.error(error);
    }
  };

  const renderSearchResults = () => {
    return searchResults.length > 0 ? (
      /* SearchTerm      */
      <section className="search-container">
        {searchResults.map((movie) => {
                const movieImageUrl =
                    "https://image.tmdb.org/t/p/w500" + 
                    (movie.backdrop_path || movie.profile_path);
                
            return (
              <div className="movie" key={movie.id}>
                <div onClick={() => navigate(`/${movie.id}`) }className="movie_column-poster">
                  <img src={movieImageUrl} alt="" className="movie_poster"/>
                </div>
              </div>
            );
        
        
      })}
        </section>
        /* SearchTerm      */
    ) : <section className='no-results'>
      <p>  "{debouncedSearchTerm}"   .</p>
    </section>
  }
  return (
    <div>{renderSearchResults()}</div>
  )
}
